// 해당 파일은 SCSS 변수를 정의하는 파일입니다.
// 이 파일은 색상, 폰트, 여백, 반응형 디자인을 위한 미디어 쿼리 등의 변수를 정의하여,
// 전체 스타일에서 일관되게 사용할 수 있도록 합니다.
// abstracts/_variables.scss
// Color Palette

// =============================
// Typography
// =============================
$font-default: "Pretendard", sans-serif;
$font-logo: "Paperlogy-8ExtraBold", sans-serif;
// $font-body: "BMJUA", sans-serif;
// $font-body: "GmarketSansMedium", sans-serif;
$font-body: "Pretendard-Regular", sans-serif;

// =============================
// ⚪ Base Colors
// =============================
$white: #ffffff;
$black: #000000;

// =============================
// ✨ Accent / Branding Colors
// =============================
$color-white: #ffffff;
$color-black: #1e2b3c;
$color-gray-100: #f9fafb;
$color-gray-200: #f2f4f6;
$color-gray-300: #e5e8eb;
$color-gray-400: #d1d6db;
$color-gray-500: #8b95a1;
$color-gray-600: #4b5563;
$color-gray-700: #374151;
$color-gray-800: #1f2937;
$color-gray-900: #111827;

$color-primary: #3182f6;
$color-purple: #805ad5;
$color-danger: #ff5a5a;

$color-primary: #1a73e8;
$color-primary-rgb: 26, 115, 232;
$color-success: #4caf50;
$color-success-dark: #3e8e41;

// =============================
// 🌈 blur
// =============================
$default-blur-bg: rgba(255, 255, 255, 0.8);

// =============================
// 😇 shadow
// =============================
$shadow-card: 0 1px 4px rgba(0, 0, 0, 0.05);
$shadow-default: 0 8px 20px rgba(0, 0, 0, 0.08);
$shadow-scrolled: 0 2px 8px rgba(0, 0, 0, 0.05);

// Radius (둥글기)
$radius-sm: 4px;
$radius-md: 8px;
$radius-lg: 12px;
$radius-xl: 16px;
$radius-pill: 9999px;

// Spacing (여백)
$padding-sm: 0.5rem; // 8px
$padding-md: 1rem; // 16px
$padding-lg: 1.5rem; // 24px
$margin-sm: 0.5rem;
$margin-md: 1rem;
$margin-lg: 1.5rem;

// Gap (요소 간 간격)
$gap-xs: 0.5rem; // 8px
$gap-sm: 1rem; // 16px
$gap-md: 1.5rem; // 24px
$gap-lg: 2rem; // 32px

// =============================
// ⚙ Common Font Sizes (추가)
// =============================
$font-size-header-ml: 1.8rem;
$font-size-header: 1.5rem;
$font-size-header-md: 1.4rem;
$font-size-header-sm: 1.3rem;
$font-size-header-xs: 1.15rem;
$font-size-nav: 1.1rem;
$font-size-nav-md: 0.9rem;
$font-size-nav-sm: 0.85rem;
$font-size-nav-xs: 0.8rem;

$font-size-lg: 1rem;
$font-size-md: 0.8rem;
$font-size-sm: 0.7rem;

$font-size-body: 0.6rem;

// =============================
// Breakpoints
// =============================
$breakpoint-sm: 640px;
$breakpoint-md: 768px;
$breakpoint-lg: 1024px;
$breakpoint-xl: 1280px;
$breakpoint-2xl: 1536px;
$breakpoint-3xl: 1920px;
$breakpoint-4xl: 2560px;
$breakpoint-5xl: 3840px;

// =============================
// 사용하는 변수
// =============================
$text-color: $color-black;
$text-subtle: $color-gray-500;
$background-color: $color-gray-100;
$card-bg: $color-white;
$border-dark: 1px solid $color-gray-300;
$border-light: 1px solid $color-gray-200;
